import React from "react";
import Layout from "../components/layout";
import Meeting from "../components/meeting";
import Button from "../components/button";
import Tabs from "../components/tabs";
import DB from "../components/db";


const StartMeeting = props => (
  <div className="level" style={{marginBottom: `10px`}}>
    <div className="level-item">
      <Button onClick= {props.onClick} class="fas fa-lg fa-people-carry has-text-info" text="Fara Taro"/>
    </div>
  </div>
)

class MeetingsList extends React.Component {

  constructor(props) {
    super(props);
    this.newMeeting = this.newMeeting.bind(this);
    this.state = {
      meetings: []
    };
    this.dates = [
      "24-05-19", "11-11-19", "14-03-19", "01-12-19", "12-07-19", "09-02-19"
    ]
  }

  componentDidMount() {
    DB.table('meetings')
      .toArray()
      .then((meetings) => {
        this.setState({ meetings });
      });
  }

  newMeeting() {
    console.log("button pressed");
    const date = new Date(+(new Date()) - Math.floor(Math.random()*10000000000));
    const meeting = {
      people: Math.floor(Math.random() * 30) + 1,
      date: date.toDateString(),
      ins: Math.floor(Math.random() * 30) + 1,
      outs: Math.floor(Math.random() * 20) + 1
    }
    DB.table('meetings')
      .add(meeting)
      .then((id) => {
        const newMeeting = [meeting, ...this.state.meetings];
        this.setState({ meetings: newMeeting });
      })
      .catch((error) => {
        console.log(error);
      });

    // this.setState({
    //   meetings: [
    //     {people: Math.floor(Math.random() * 30) + 1, date: this.dates[Math.floor(Math.random() * (this.dates.length - 1))], ins: Math.floor(Math.random() * 30) + 1, outs: Math.floor(Math.random() * 20) + 1},
    //     ...this.state.meetings
    //   ]
    // })
  }

  render() {
    return (
      <div>
        <StartMeeting onClick={this.newMeeting}/>
        {this.state.meetings.map((meeting, index) => (
          <Meeting key={meeting.id} people={meeting.people} date={meeting.date} ins={meeting.ins} outs={meeting.outs}/>
        ))}
      </div>
   )
  }
}

export default () => (
  <Layout>
    <Tabs />
    <MeetingsList />
  </Layout>
);
