import Dexie from 'dexie';

const db = new Dexie('vslaDB');
db.version(1).stores({
  fund: '++id, people, ins, outs',
  members: '++id, &name, phone, shares, loans, &imgIndex',
  meetings: '++id, people, &date, ins, outs',
  attendance: '++, meetingId, memberId, [meetingId+memberId]'
});

db.fund.add({
  people: 30, inputs: 300, outputs: 45
});

db.members.bulkAdd([
  { name: "Amina", phone: 173739279, shares: 31, loans: 4, imgIndex: Math.floor(Math.random() * (14) )},
  { name: "Fara", phone: 24627835, shares: 15, loans: 34, imgIndex: Math.floor(Math.random() * (14)) },
  { name: "Ladi", phone: 18394026, shares: 25, loans: 4, imgIndex: Math.floor(Math.random() * (14)) },
  { name: "Hannah", phone: 17363898, shares: 13, loans: 3, imgIndex: Math.floor(Math.random() * (14)) }
]);

db.meetings.bulkAdd([
  { people: "6", date: "Mon Oct 12 2019", ins: "10", outs: "3" },
  { people: "10", date: "Wed Nov 09 2018", ins: "100", outs: "80" },
  { people: "15", date: "Fri Oct 21 2017", ins: "145", outs: "30" },
  { people: "21", date: "Sun Jun 19 2016", ins: "10", outs: "13" },
  { people: "9", date: "Wed Oct 03 2015", ins: "30", outs: "33" },
  { people: "18", date: "Sat Feb 29 2014", ins: "10", outs: "53" },
  { people: "20", date: "Mon Oct 01 2013", ins: "70", outs: "3"}
]);

export default db;
