import React from "react";
import { Link } from "gatsby";

const ListLink = props => (
  <li>
    <Link activeClassName="is-active" to={props.to}>
      <div className="level">
        <div className="level-item">{props.children}</div>
        <div className="level-item">
          <span className="icon fa-lg">
            <i className={props.class} aria-hidden="true"></i>
          </span>
        </div>
      </div>
    </Link>
  </li>
);

export default () => (
  <div className="tabs is-centered is-small" style={{marginBottom: `4px`}}>
    <ul className="is-mobile">
      <ListLink class="fas is-medium fa-2x fa-toolbox" to="/fund/">Asusu</ListLink>
      <ListLink class="fas is-medium fa-2x fa-people-carry" to="/meetings/">Taro</ListLink>
      <ListLink class="fas is-medium fa-2x fa-users" to="/members/">Mambobi</ListLink>
    </ul>
  </div>
);
